body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
    display: grid;
    margin: 0 auto;
    width: 1000px;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}

.preloaderWrapper {
    margin: 0 auto;
    width: 1000px;
    height: 700px;
    background-color: #1a2e53;
    position: relative;
}
.preloaderTriangle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-80%, -60%);
}

.app-wrapper-content {
    grid-area: c;
    background-color: #1a2e53;
    border-top: 1px solid #41619c;
    border-left: 1px solid #0b1830;
}






.Navbar_nav__3K3FK{
  background-color: #1a2e53;
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-top: 1px solid #41619c;
  border-right: 1px solid #41619c;
}
/*настройка кнопок менюшки*/
.Navbar_items__3vuzC .Navbar_item__1HwlQ a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  padding: 6px 5px;
  background: #2196f3;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  overflow: hidden;
  transition: 0.5s;
  z-index: 1;
}
/*прозрачность меню при наведении*/
.Navbar_items__3vuzC:hover .Navbar_item__1HwlQ a {
  opacity: 0.3;
}
/*изменение кнопки меню при наведении*/
.Navbar_items__3vuzC .Navbar_item__1HwlQ a:hover {
  transform: scale(1.4);
  z-index: 1000;
  background: #ff3f81;
  opacity: 1;
}
/*активная кнопочка*/
.Navbar_items__3vuzC .Navbar_item__1HwlQ a.Navbar_activeLink__2yH5U {
  color: #2196f3;
  background: #fff;
}
/*настройка градиента возле каждоый кнопки меню*/
.Navbar_items__3vuzC .Navbar_item__1HwlQ a:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100px;
  width: 80px;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(
          255,255,255,0.8), transparent);
  transform: skewX(35deg);
  transition: 0s;
}
/*активация движения градиента при намедении мышки*/
.Navbar_items__3vuzC .Navbar_item__1HwlQ a:hover:before {
  left: calc(100% + 100px);
  transition: 0.5s;
  transition-delay: 0.2s;
}

